@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
  
}

body {
  background-color: #eeeeee;
}
.navbar {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  background-color: rgba(1,1,1,0.1);
  
}
.navbar h1 {
  color: #03a9f4;
  margin-left:100px
}
.navbar .links {
  margin-left:450px ;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #03a9f4;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* idea previews / list */


.ideas-preview {
  padding: 10px 16px;
  margin: 20px 0;
  width: 250px;
  border-bottom: 1px solid black;
  background-color: #fff;
  text-align: center;
  border-radius: 8px;
  transition: all 1s ease-in-out;
}
.ideas-preview:hover {
  box-shadow: 1px 3px 5px rgba(1,1,1,0.1);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.ideas-preview h2 {
  font-size: 20px;
  color: #03a9f4;
  margin-bottom: 8px;
}
.ideas-preview img {
  width: 200px;
  border-radius: 6px;
}

.ideas-preview a {
  text-decoration: none;
}

/* ideas details page */
.ideas-details {
  text-align: center;
}
.ideas-details h2 {
  font-size: 20px;
  color: #03a9f4;
  margin-bottom: 10px;
}

.ideas-details img {
  width: 400px;
  height: 400px;
  border-radius: 10px;
}
.ideas-details .content-body {
  margin: 20px 0;
}
.ideas-details button {
  background: #03a9f4;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.idea-details #count {
  margin-top: 10px;
}

.reviwe-part {
  position: relative;
  float: left;
  display: -webkit-flex;
  display: flex;
  margin-top: 5px;
  border-radius: 3px;
}

.reviwe-part  p {
  color: red;
}

/* idea details page */
.idea-details h2 {
  font-size: 20px;
  color: #03a9f4;
  margin-bottom: 10px;
}
.idea-details div {
  margin: 20px 0;
}
.idea-details button {
  background: #03a9f4;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.like-button {
  font-size: 24px;

}

.unlike {
  font-size: 24px;
  color: #03a9f4;
}

/* create new idea form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #03a9f4;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #03a9f4;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
